@import "../../styles/mixins";

.why {
    margin-bottom: 90px;

    @include md {
        margin-bottom: 40px;
    }

    &_container {
        @include container;
    }

    &_heading {
        @include heading;
    }

    &_list {
        display: flex;

        @include md {
            width: 100%;
            overflow-x: scroll;

            &::-webkit-scrollbar {
                display: none !important;
            }
        }
    }

    &_item {
        margin-right: 30px;
        position: relative;

        &:last-of-type {
            margin-right: 0;
        }
    }

    &_mobile {
        background-color: #ffc91e;
        width: 285px;
        min-height: 200px;
        padding: 20px;
        margin-right: 20px;

        &:last-of-type {
            margin-right: 0;
        }

        h3 {
            margin-bottom: 10px;
            text-transform: uppercase;
        }

        p {
            font-size: 14px;

            @include sm {
                font-size: 12px;
            }
        }
    }

    &_img {
        width: 275px;
        height: 400px;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &_block {
        position: absolute;
        width: 275px;
        height: 115px;
        bottom: -10px;
        left: -10px;
        background-color: #ffc91e;
        padding: 20px 10px 20px 20px;
        transition: all .3s;

        h3 {
            text-transform: uppercase;
            margin-bottom: 10px;
            font-size: 20px;
        }

        p {
            font-size: 14px;
        }
    }

    &_hover {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: rgba(#ffc91e, .8);
        padding: 10px;
        transition: all .3s;

        h3 {
            text-transform: uppercase;
            margin-bottom: 20px;
            font-size: 20px;
        }

        p {
            font-size: 16px;
        }
    }
}