@import "../../styles/mixins";

.services {
    margin-bottom: 80px;

    @include md {
        margin-bottom: 20px;
    }

    &_container {
        @include container;
        display: flex;
        justify-content: space-between;

        @include md {
            flex-direction: column;
            margin: 0;
            width: 100%;
            max-width: 100%;
        }
    }

    &_menu {
        background-color: #fff;
        margin-right: 40px;
        min-width: 30%;

        @include md {
            margin-right: 0;
            min-height: 450px;
            // height: 450px;
            margin-bottom: 20px;
        }

    }

    &_heading {
        background-color: #ffc91e;
        text-align: center;
        padding: 20px 0;
        margin-bottom: 30px;

        h2 {
            font-size: 24px;
            text-transform: uppercase;

            @include sm {
                font-size: 20px;
            }
        }
    }

    &_item {
        text-transform: uppercase;
        margin-bottom: 30px;
        margin-left: 20px;

        @include md {
            ul {
                transition: all .4s;
                position: relative;
                top: -20px;
            }
        }

        &:last-of-type {
            margin-bottom: 0;
        }

        h3 {
            margin-bottom: 10px;

            @include md {
                font-size: 18px;
            }

            @include sm {
                font-size: 16px;
            }
        }
    }

    &_subItem {
        margin-left: 0;
        font-size: 14px;
        text-transform: none;
        padding-left: 20px;
        padding-right: 65px;
        margin-bottom: 10px;
        cursor: pointer;

        &:last-of-type {
            margin-bottom: 0;
        }

        @include sm {
            font-size: 12px;
        }
    }

    &_content {
        background-color: #fff;
        padding: 20px 20px 40px 20px;
    }

    &_text {
        margin-bottom: 40px;

        @include md {
            margin-bottom: 20px;
        }

        h3 {
            margin-bottom: 20px;

            @include md {
                font-size: 18px;
            }
        }

        p {
            margin-bottom: 40px;
            line-height: 25px;

            @include md {
                font-size: 14px;
                line-height: 20px;
            }

            @include sm {
                font-size: 12px;
                line-height: 18px;
            }
        }

        button {
            @include yellow_button;
        }
    }

    &_images {
        &_list {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            column-gap: 10px;

            @include md {
                column-gap: 4px;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

        }
    }

    &_mainImage {
        width: 100%;
        height: 440px;
        margin-bottom: 10px;

        @include md {
            height: 300px;
        }

        @include sm {
            height: 200px;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}