@import "../../styles/mixins";

.hero {
    height: 95vh;
    position: relative;

    @include sm {
        height: 90vh;
    }

    &_overlay {
        position: absolute;
        display: block;
        top: 0;
        bottom: 0;
        left: 0;
        width: 47vw;
        background: #282d33;
        z-index: 1;
    }

    &_container {
        display: flex;
        align-items: center;
        height: 100%;
        padding: 0 100px;
        z-index: 2;
        position: absolute;

        @include sm {
            padding: 0 25px 0 40px;
        }

        &_text {
            max-width: 560px;
            color: white;

            h1 {
                text-transform: uppercase;
                font-size: 54px;
                font-weight: bold;
                margin-bottom: 23px;
                padding-left: 32px;
                border-left: 10px solid #ffc91e;

                @include md {
                    font-size: 40px;
                    padding-left: 18px;
                    border-left: 8px solid #ffc91e;
                }

                @include sm {
                    font-size: 25px;
                    padding-left: 10px;
                    border-left: 5px solid #ffc91e;
                }
            }

            h3 {
                margin-bottom: 20px;
                font-weight: 500;
                font-size: 20px;
                padding-left: 50px;

                @include md {
                    padding-left: 33px;
                }

                @include sm {
                    padding-left: 20px;
                    font-size: 16px;
                }
            }

            span {
                color: #ffc91e;
            }

            button {
                @include yellow_button;
            }
        }
    }

    &_button_container {
        padding-left: 50px;

        @include md {
            padding-left: 33px;
        }

        @include sm {
            padding-left: 20px;
        }
    }

    &_desc {
        position: absolute;
        z-index: 2;
        bottom: 0;
        left: 0;
        right: 0;
        transform: translateY(50%);

        &::-webkit-scrollbar {
            display: none !important;
        }

        @include md {
            // left: unset;
            left: 0;
            right: unset;
            width: 100%;
            overflow-x: scroll;
            transform: translateY(100%);
        }

        &_container {
            @include container;

            @include md {
                margin: 0;
            }
        }

        ul {
            display: flex;
            background-color: #fff;
            box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.2);

            @include md {
                min-width: 100%;
            }
        }

        li {
            width: 25%;
            padding: 30px 18px 50px 18px;
            text-align: center;
            position: relative;
            transition: all .3s;
            background-color: #fff;

            &:after {
                content: '';
                display: block;
                width: 2px;
                height: 128px;
                position: absolute;
                right: 0;
                top: 50%;
                background-color: rgba(0, 0, 0, 0.08);
                transform: translateY(-50%);
            }

            &:hover {
                background-color: #ffc91e;
                transform: scaleY(1.07);
                box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
            }

            @include md {
                min-width: 300px;
                height: 300px;
            }

        }

        img {
            margin-bottom: 27px;
        }

        h2 {
            font-size: 20px;
            text-transform: uppercase;
            margin-bottom: 20px;
        }
    }
}